body,
html {
  background: #f0f0f0;
  position: relative;
  overflow-x: hidden;
}

header,
section,
footer {
  padding: 80px 0;
}

h1 {
  font: 400 90px/1 "Comic CAT", sans-serif;
  text-transform: uppercase;
  color: #0082c9;
}

.btn__red {
  padding: 15px 22px;
  border: none;
  border-radius: 55px;
  background: #ff7f56;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  font: 400 18px/1 "SF Pro Display", sans-serif;
  color: #fff;
  transition: all 0.3s;
  width: max-content;
}
.btn__red:hover {
  background: #ff6635;
}
.btn__trans {
  border: 1px solid #bdd9e8;
  padding: 14px 21px;
  border-radius: 55px;
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  font: 400 18px/1 "SF Pro Display", sans-serif;
  color: #0082c9;
  transition: all 0.3s;
  width: max-content;
}
.btn__trans:hover {
  color: #fff;
  border: 1px solid #0082c9;
  background: #0082c9;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  box-sizing: content-box;
}
.container__title {
  font: 500 15px/1.2 "SF Pro Display", sans-serif;
  text-transform: uppercase;
  color: #0082c9;
}
.container__wrapper {
  display: grid;
  grid-template-columns: 290px 1fr;
  gap: 20px;
  align-items: start;
}

.nav {
  padding: 19px 0;
}
.nav__logo {
  display: block;
  width: 100%;
  max-width: 216px;
}
.nav__logo img {
  width: 100%;
}
.nav .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
}
.nav .btn__red::before {
  content: "";
  display: block;
  width: 18px;
  height: 19px;
  background: url("../img/heart_trans.svg") center no-repeat;
  background-size: contain;
}
.nav .btn__red:hover {
  background-size: contain;
}
.nav .btn__red:hover::before {
  background: url("../img/heart_full.svg") center no-repeat;
}

.header__wrapper {
  margin: 45px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}
.header__wrapper img {
  width: 100%;
  border-radius: 30px;
  height: 400px;
  object-fit: cover;
  opacity: 0;
  transition: all 0.5s 1s;
  transform: translateY(50px);
}
.header__slider {
  margin: 45px 0 0;
}
.header__name {
  font: 400 90px/1 "Comic CAT", sans-serif;
  text-transform: uppercase;
  color: #0082c9;
  max-width: calc(100% - 100px);
}
.header__name span {
  display: block;
  opacity: 0;
  transition: all 0.5s 0.5s;
  transform: translateY(50px);
}
.header__name span + span {
  transition-delay: 0.8s;
}
.header__text {
  font: 400 18px/1.4 "SF Pro Display", sans-serif;
  color: #4686a8;
  max-width: 444px;
  opacity: 0;
  transition: all 0.5s 1s;
  transform: translateY(50px);
}
.header .btn__trans {
  margin: 35px 0 0;
  opacity: 0;
  transition: all 0.3s, transform 0.5s 1s, opacity 0.5s 1s;
  transform: translateY(50px);
}
.header__slide {
  opacity: 0 !important;
  transition: all 0.5s 0s !important;
}
.header__slide.swiper-slide-active {
  opacity: 1 !important;
}
.header__slide.swiper-slide-active .btn__trans {
  opacity: 1;
  transform: translateY(0);
}
.header__slide.swiper-slide-active .header__wrapper img {
  opacity: 1;
  transform: translateY(0);
}
.header__slide.swiper-slide-active .header__name span {
  opacity: 1;
  transform: translateY(0);
}
.header__slide.swiper-slide-active .header__text {
  opacity: 1;
  transform: translateY(0);
}
.header .swiper-button-prev,
.header .swiper-button-next {
  opacity: 1;
  width: 57px;
  height: 47px;
  margin: 0;
  right: auto;
  top: auto;
  transform: none;
  left: 0;
  bottom: 0;
}
.header .swiper-button-prev path,
.header .swiper-button-next path {
  transition: all 0.3s;
}
.header .swiper-button-prev::after,
.header .swiper-button-next::after {
  content: none;
}
.header .swiper-button-prev:hover path, .header .swiper-button-prev.swiper-button-disabled path,
.header .swiper-button-next:hover path,
.header .swiper-button-next.swiper-button-disabled path {
  fill: #82aec5;
}
.header .swiper-button-next {
  left: 82px;
}
.header .swiper-pagination {
  width: max-content;
  left: auto;
  top: auto;
  transform: none;
  right: 0;
  margin: 0;
  bottom: 435px;
  font: 500 35px/1.2 "SF Pro Display", sans-serif;
  color: #bdd9e8;
}
.header .swiper-pagination .swiper-pagination-current {
  color: #0082c9;
}

.fees__wrapper {
  border-bottom: 1px solid #bdd9e8;
}
.fees__prev {
  transition: all 0.3s;
  position: relative;
  overflow: hidden;
  border-radius: 20px;
}
.fees__prev img {
  width: 290px;
  height: 290px;
  object-fit: cover;
}
.fees__info {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 20px;
}
.fees__info p {
  color: #0082c9;
  transition: all 0.3s;
}
.fees__for {
  font: 400 16px/1.3 "SF Pro Display", sans-serif;
  color: #5d98b9 !important;
}
.fees__name {
  font: 500 35px/1.2 "SF Pro Display", sans-serif;
}
.fees__price {
  font: 400 20px/1.3 "SF Pro Display", sans-serif;
  margin: auto 0 0;
}
.fees__item {
  padding: 35px 0;
  border-top: 1px solid #bdd9e8;
  display: grid;
  grid-template-columns: 290px 1fr 40px;
  gap: 30px;
}
.fees__item:hover .fees__info p {
  color: #82aec5 !important;
}
.fees__item:hover .fees__prev {
  border-radius: 70px;
}
.fees .btn__trans {
  margin: 35px 0 0;
}

.stat__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 10px;
}
.stat__item {
  border-radius: 20px;
  background: #fff;
  padding: 30px 25px 25px;
  min-height: 380px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.stat__item:nth-child(2) {
  grid-column: 3/4;
}
.stat__val {
  font: 500 70px "SF Pro Display", sans-serif;
  text-transform: uppercase;
  color: #0082c9;
}
.stat__text {
  font: 400 18px/1.4 "SF Pro Display", sans-serif;
  color: #4686a8;
}

.sup {
  color: #fff;
  background: #0082c9;
}
.sup .container__title {
  color: #e8f2ff;
}
.sup__title {
  font: 400 50px/1.1 "Comic CAT", sans-serif;
  text-transform: uppercase;
  color: #fff;
}
.sup__title span {
  transition: all 0.5s;
  opacity: 0;
  transform: translateY(50px);
  transition-delay: 0s;
  display: block;
}
.sup__title span + span {
  transition-delay: 0.3s;
}
.sup__title span + span + span {
  transition-delay: 0.6s;
}
.sup__title.element-show span {
  opacity: 1;
  transform: translateY(0);
}
.sup__text {
  margin: 40px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}
.sup__text p {
  font: 400 18px/1.4 "SF Pro Display", sans-serif;
  color: #d1e9f6;
}
.sup .btn__red {
  margin: 30px 0 0;
}

.method__item {
  border-top: 1px solid #bdd9e8;
  padding: 35px 0;
  display: grid;
  grid-template-columns: 290px 1fr 147px;
  gap: 10px;
}
.method__item:last-child {
  padding: 35px 0 0;
}
.method__name {
  font: 500 25px/1.3 "SF Pro Display", sans-serif;
  color: #0082c9;
}
.method__info {
  display: flex;
  flex-direction: column;
}
.method__info p {
  font: 400 18px/1.4 "SF Pro Display", sans-serif;
  color: #4686a8;
  margin: 0 0 10px;
  max-width: 370px;
}
.method__info .btn__trans {
  margin: auto 0 0;
}
.method__prev {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 147px;
  border-radius: 20px;
  background: #fff;
}

.about__name {
  font: 400 50px/1.1 "Comic CAT", sans-serif;
  text-transform: uppercase;
  color: #0082c9;
}
.about__name span {
  transition: all 0.5s;
  opacity: 0;
  transform: translateY(50px);
  display: inline-block;
}
.about__name.element-show span {
  opacity: 1;
  transform: translateY(0);
}
.about__wrapper {
  margin: 40px 0 0;
  display: grid;
  grid-template-columns: 280px 1fr;
  gap: 20px;
}
.about__text {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.about__text p {
  font: 400 18px/1.4 "SF Pro Display", sans-serif;
  color: #4686a8;
}
.about__text .btn__trans {
  margin: auto 0 0;
}
.about__prev img {
  height: 342px;
  width: 100%;
  object-fit: cover;
  border-radius: 30px;
}

.parthner {
  background: #0082c9;
  position: relative;
  overflow: hidden;
}
.parthner .container__title {
  color: #e8f2ff;
}
.parthner__wrapper {
  grid-template-columns: 1fr 1fr 1fr;
  gap: 10px;
  display: grid;
}
.parthner__item {
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}
.parthner__item img {
  max-width: 200px;
  width: 100%;
  height: 80px;
  object-fit: contain;
}
.parthner__run {
  max-height: 400px;
}

.news__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
}
.news__prev {
  position: relative;
  overflow: hidden;
  border-radius: 10px;
  transition: all 0.3s;
}
.news__prev img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}
.news__row {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 15px;
  margin: 0 0 auto;
}
.news__row p {
  font: 400 14px/1.4 "SF Pro Display", sans-serif;
  color: #77aecc;
}
.news__name {
  font: 500 25px/1.3 "SF Pro Display", sans-serif;
  color: #0082c9;
  max-width: calc(100% - 50px);
  transition: all 0.3s;
}
.news__link {
  position: absolute;
  bottom: 25px;
  right: 25px;
}
.news .btn__trans {
  margin: 35px 0 0;
}
.news__item {
  min-height: 440px;
  display: flex;
  flex-direction: column;
  gap: 18px;
  padding: 25px;
  border-radius: 20px;
  background: #fff;
  position: relative;
}
.news__item:hover .news__prev {
  border-radius: 70px;
}
.news__item:hover .news__name {
  color: #82aec5;
}

.footer {
  background: #0082c9;
  padding-bottom: 36px;
}
.footer .container__title {
  color: #e8f2ff;
}
.footer__name {
  font: 400 14px/1.4 "SF Pro Display", sans-serif;
  color: #91cbea;
}
.footer__list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 30px;
  row-gap: 60px;
}
.footer__list ul {
  margin: 20px 0 0;
  display: grid;
  grid-template-columns: 1fr;
  gap: 10px;
}
.footer__list ul a {
  font: 400 18px/1.2 "SF Pro Display", sans-serif;
  color: #fff;
  transition: all 0.3s;
}
.footer__list ul a:hover {
  color: #91cbea;
}
.footer__contact {
  margin: 80px 0 0;
  padding: 25px 0 0;
  border-top: 1px solid #349ed7;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 30px;
}
.footer__contact_wrapper {
  grid-column: 1/3;
}
.footer__contact_link {
  margin: 15px 0 0;
  display: grid;
  grid-template-columns: 1fr;
  gap: 10px;
}
.footer__contact_link a {
  font: 500 25px/1.3 "SF Pro Display", sans-serif;
  color: #fff;
  transition: all 0.3s;
}
.footer__contact_link a:hover {
  color: #91cbea;
}
.footer__contact_row {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  margin: 15px 0 0;
  gap: 10px;
}
.footer__contact_row a {
  font: 400 16px/1.4 "SF Pro Display", sans-serif;
  text-align: center;
  color: #fff;
  padding: 13px 15px;
  width: 100%;
  max-width: 150px;
  border: 1px solid #2996d2;
  border-radius: 55px;
  transition: all 0.3s;
  position: relative;
  overflow: hidden;
}
.footer__contact_row a span {
  display: block;
  transition: all 0.3s;
  position: relative;
}
.footer__contact_row a::before {
  content: attr(link);
  position: relative;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -200%);
  transition: all 0.3s;
}
.footer__contact_row a:hover {
  border: 1px solid #fff;
}
.footer__contact_row a:hover::before {
  transform: translate(-50%, -50%);
}
.footer__contact_row a:hover span {
  transform: translateY(200%);
}
.footer__soc {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
  margin: 0 0 auto;
}
.footer__soc_wrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.footer__soc a {
  position: relative;
  width: 56px;
  height: 56px;
  border-radius: 100%;
  border: 1px solid #2996d2;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  transition: all 0.3s;
}
.footer__soc a img {
  width: 26px;
  height: 26px;
  display: block;
  position: relative;
  transition: all 0.3s;
}
.footer__soc a::before {
  content: "";
  position: relative;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -200%);
  transition: all 0.3s;
  background: var(--bg) center no-repeat;
  background-size: contain;
  width: 26px;
  height: 26px;
  display: block;
  position: absolute;
}
.footer__soc a:hover {
  border: 1px solid #fff;
}
.footer__soc a:hover::before {
  transform: translate(-50%, -50%);
}
.footer__soc a:hover img {
  transform: translateY(200%);
}
.footer__sub {
  margin: 100px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 30px;
}
.footer__sub a {
  font: 400 14px/1.4 "SF Pro Display", sans-serif;
  color: #91cbea;
  text-decoration: underline;
  text-decoration-skip-ink: none;
  display: block;
  transition: all 0.3s;
}
.footer__sub a:hover {
  color: #fff;
}
.footer__sub p {
  font: 400 14px/1.4 "SF Pro Display", sans-serif;
  color: #91cbea;
}
.footer__sub p a {
  display: inline;
  color: #fff;
}
.footer__sub p a:hover {
  color: #91cbea;
}

@media (max-width: 1024px) {
  header,
  section,
  footer {
    padding: 40px 0;
  }
  h1 {
    font-size: 50px;
  }
  .container__title {
    font-size: 11px;
  }
  .container__wrapper {
    grid-template-columns: 130px 1fr;
  }
  .btn__red, .btn__trans {
    font-size: 14px;
    padding: 11px 13px;
  }
  .btn__trans {
    padding: 10px 12px;
  }
  .nav {
    padding: 13px 0;
  }
  .nav .btn__red::before {
    content: none;
  }
  .nav__logo {
    max-width: 112px;
  }
  .header__slider {
    margin: 20px 0 0;
  }
  .header__name {
    font-size: 50px;
  }
  .header .swiper-pagination {
    font-size: 19px;
    bottom: 295px;
  }
  .header__wrapper {
    margin: 25px 0 0;
  }
  .header__wrapper img {
    height: 270px;
  }
  .header__text {
    font-size: 13px;
  }
  .header .btn__trans {
    margin: 20px 0 0;
  }
  .header .swiper-button-next,
  .header .swiper-button-prev {
    width: 36px;
    height: 30px;
  }
  .header .swiper-button-next {
    left: 51px;
  }
  .fees__info {
    gap: 10px;
  }
  .fees__prev {
    border-radius: 15px;
  }
  .fees__prev img {
    width: 142px;
    height: 156px;
  }
  .fees__item {
    padding: 20px 0;
    grid-template-columns: 142px 1fr 20px;
    gap: 15px;
  }
  .fees__item:hover .fees__prev {
    border-radius: 35px;
  }
  .fees__for {
    font-size: 12px;
  }
  .fees__name {
    font-size: 25px;
  }
  .fees__price {
    font-size: 13px;
  }
  .fees__link svg {
    width: 20px;
    height: 20px;
  }
  .fees .btn__trans {
    margin: 20px 0 0;
  }
  .stat__item {
    min-height: 240px;
    padding: 15px;
  }
  .stat__val {
    font-size: 40px;
  }
  .stat__text {
    font-size: 13px;
  }
  .sup__title {
    font-size: 32px;
  }
  .sup__text {
    margin: 20px 0 0;
    gap: 10px;
  }
  .sup__text p {
    font-size: 13px;
  }
  .sup .btn__red {
    margin: 20px 0 0;
  }
  .method__item {
    padding: 20px 0;
    grid-template-columns: 1fr 100px;
    gap: 20px;
  }
  .method__item:last-child {
    padding: 20px 0 0;
  }
  .method__prev {
    grid-column: 2/3;
    grid-row: 1/3;
    height: 100px;
    border-radius: 15px;
  }
  .method__prev svg {
    width: 44px;
    height: 40px;
  }
  .method__name {
    font-size: 19px;
  }
  .method__info p {
    font-size: 13px;
  }
  .about__name {
    font-size: 32px;
  }
  .about__wrapper {
    margin: 20px 0 0;
    gap: 20px;
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .about__text p {
    font-size: 13px;
  }
  .about__prev img {
    height: 237px;
  }
  .news__item {
    padding: 15px;
  }
  .footer__list {
    grid-template-columns: 1fr 1fr;
    gap: 30px;
  }
  .footer__contact {
    grid-template-columns: 1fr;
  }
  .footer__contact_wrapper {
    grid-column: auto;
  }
  .footer__sub {
    margin: 50px 0 0;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
  .footer__sub div {
    grid-column: 1/2;
  }
  .footer__soc {
    position: relative;
  }
  .footer__soc_wrapper {
    position: relative;
  }
  .footer__soc_wrapper .footer__name:last-child {
    position: absolute;
    right: 0;
    top: calc(100% + 50px);
    width: calc(50% - 10px);
  }
}
@media (max-width: 768px) {
  .stat__wrapper {
    grid-template-columns: 1fr 1fr;
  }
  .stat__item {
    grid-column: auto !important;
  }
  .about__wrapper {
    grid-template-columns: 1fr;
  }
  .about__prev {
    order: -1;
  }
  .parthner__item {
    border-radius: 15px;
  }
  .parthner__item img {
    max-width: 100px;
    height: 40px;
  }
  .news__prev img {
    height: 110px;
  }
  .news__item {
    gap: 10px;
    min-height: 270px;
  }
  .news__item:hover .news__prev {
    border-radius: 40px;
  }
  .news__row p {
    font-size: 11px;
  }
  .news__link {
    display: none;
  }
  .news__name {
    font-size: 19px;
  }
  .news .btn__trans {
    margin: 20px 0 0;
  }
  .footer__name {
    font-size: 8px;
  }
  .footer__list ul {
    margin: 10px 0 0;
    gap: 5px;
  }
  .footer__list ul a {
    font-size: 13px;
  }
  .footer__contact {
    margin: 40px 0 0;
    padding: 15px 0 0;
    gap: 20px;
  }
  .footer__contact_link {
    margin: 10px 0 0;
  }
  .footer__contact_link a {
    font-size: 19px;
  }
  .footer__contact_row {
    gap: 8px;
  }
  .footer__contact_row a {
    padding: 6px;
    max-width: 92px;
    font-size: 13px;
  }
  .footer__soc {
    gap: 8px;
  }
  .footer__soc_wrapper {
    gap: 10px;
  }
  .footer__soc_wrapper .footer__name:last-child {
    font-size: 11px;
  }
  .footer__soc a {
    width: 42px;
    height: 42px;
  }
  .footer__soc a img, .footer__soc a::before {
    width: 19px;
    height: 19px;
  }
  .footer__sub {
    gap: 15px;
  }
  .footer__sub p {
    font-size: 11px;
  }
  .footer__sub a {
    font-size: 11px;
  }
}
@media (max-width: 600px) {
  .container {
    padding: 0 10px;
  }
  .container__wrapper {
    grid-template-columns: 1fr;
    gap: 30px;
  }
  .nav {
    padding: 11px 0;
  }
  .header__name {
    font-size: 42px;
    max-width: 100%;
  }
  .header .swiper-wrapper {
    padding: 0 0 80px;
  }
  .header .swiper-pagination {
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    text-align: center;
    padding: 17px 0;
    border-top: 1px solid #bdd9e8;
    border-bottom: 1px solid #bdd9e8;
    z-index: -1;
    font-size: 14px;
  }
  .header .swiper-button-next,
  .header .swiper-button-prev {
    bottom: 15px;
    width: 30px;
    height: 25px;
  }
  .header .swiper-button-next {
    left: auto;
    right: 0;
  }
  .header__wrapper {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  .header__wrapper div:nth-child(2) {
    order: -1;
  }
  .header__wrapper img {
    height: 185px;
  }
  .fees__item {
    padding: 15px 0;
    grid-template-columns: 106px 1fr;
    gap: 15px;
  }
  .fees__prev img {
    height: 158px;
    width: 106px;
  }
  .fees__link {
    display: none;
  }
  .stat__wrapper {
    grid-template-columns: 1fr;
  }
  .stat__item {
    min-height: 200px;
  }
  .sup__text {
    grid-template-columns: 1fr;
    gap: 0;
  }
  .method__item {
    grid-template-columns: 1fr 67px;
  }
  .method__prev {
    width: 67px;
    height: 67px;
  }
  .method__prev svg {
    width: 30px;
    height: 27px;
  }
  .about__prev img {
    height: 185px;
  }
  .parthner__run {
    margin: 0 0 0 auto;
    height: 245px;
    width: 245px;
    transform: rotate(90deg);
  }
  .parthner__wrapper {
    grid-auto-rows: 145px;
    grid-auto-columns: 75px;
    grid-template-columns: 75px 75px 75px;
  }
  .parthner__item {
    transform: rotate(-90deg) translateY(-50%);
    width: 145px;
    height: 75px;
  }
  .news__wrapper {
    overflow: auto;
    max-width: 100vw;
    margin: 0 -10px;
    padding: 0 10px;
  }
  .news__item {
    min-width: 270px;
  }
  .footer .container__title {
    display: none;
  }
  .footer__list {
    gap: 10px;
  }
  .footer__contact_wrapper {
    order: 2;
  }
  .footer__contact_link a {
    font-size: 18px;
  }
  .footer__soc_wrapper .footer__name:last-child {
    display: none;
  }
  .footer__sub {
    margin: 50px 0 0;
    gap: 15px;
    grid-template-columns: 1fr;
  }
}